header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;

}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.3rem;
    justify-content:center ;

}
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.rem;
    position: absolute;
    left: 0; 
    bottom: 3rem;
}
.header_socials:after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}
.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 28rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 5rem;
    border-radius: 7rem 7rem 0 0;
    overflow: hidden;
    padding: 3rem 0 0 0 ;

}

.scroll_down{
    position: absolute;
    right: -3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}

/* Media Query for Tablets */
@media screen and (max-width :1024px){
    header{
        height: 68vh;


    }
}

/* Media Query for Mobiles */
@media screen and (max-width :600px){
    header{
        height: 100vh;

    }
    .header_socials,.scroll_down{
        display: none;
    }
}
